<!-- -->
<template>
    <div class="body-wrap">
    <div class="body-btn-wrap">
        <Button type='primary'  @click='add' v-if='business.getIsSuperAdmin()'></Button>
    <!-- qa -->
    </div>
    	 <!-- -->
     <Modal v-model="addReceiptInfoModel"
           title=""
           :closable="false"
           :mask-closable="false"
    >
      <Form ref="addReceiptInfo" :model="addReceiptInfo" :label-width="100" label-position="right"  :rules="addReceiptInfoRules">
        <FormItem prop="name" label=":">
          <Input type="text" v-model="addReceiptInfo.name" placeholder="">
          </Input>
        </FormItem>
        <FormItem prop="phone" label=":">
          <Input type="text" v-model="addReceiptInfo.phone" placeholder="">
          </Input>
        </FormItem>
        <FormItem prop="telephoneArea" label=":">
          <Input type="text" v-model="addReceiptInfo.telephoneArea" placeholder="">
          </Input>
        </FormItem>
        <FormItem prop="telephone" label=":">
          <Input type="text" v-model="addReceiptInfo.telephone" placeholder="">
          </Input>
        </FormItem>
        <FormItem prop="telephoneExtension" label=":">
          <Input type="text" v-model="addReceiptInfo.telephoneExtension" placeholder="">
          </Input>
        </FormItem>
        <FormItem prop="postcode" label=":">
          <Input type="text" v-model="addReceiptInfo.postcode" placeholder="">
          </Input>
        </FormItem>
        <FormItem prop="country" label=":">
          <Input type="text" v-model="addReceiptInfo.country" placeholder="">
          </Input>
        </FormItem>
        <FormItem prop="addProvinceCityArea" label="//:">
	          <Cascader :data="threeCityData"  @on-change="addProvinceAndCityAndArea" v-model="addProvinceCityArea" trigger="hover"></Cascader>
	    </FormItem>
        <FormItem prop="address" class="form-item"  label=":">
	          <Input type="text" v-model="addReceiptInfo.address" placeholder="">
	          </Input>
	    </FormItem>
        <FormItem prop="isDefault" label=":">
          <Select v-model="addReceiptInfo.isDefault" transfer size="large" style="width:100px">
              <Option v-for="item in isDefaultList" :value="item.id" :key="item.id">{{ item.value }}</Option>
          </Select>
        </FormItem>
      </Form>
      <div slot='footer'>
        <Button  @click='addCancel'></Button>
        <Button type='primary' :loading='addLoading'>
          <span v-if="!addLoading" @click='addSure'></span>
          <span v-else>Loading...</span>
        </Button>
      </div>
    </Modal>
		 <!-- -->
     <Modal v-model="updateReceiptInfoModel"
           title=""
           :closable="false"
           :mask-closable="false"
    >
      <Form ref="updateReceiptInfo" :model="updateReceiptInfo" :label-width="100" label-position="right"  :rules="updateReceiptInfoRules">
        <FormItem prop="name" label=":">
          <Input type="text" v-model="updateReceiptInfo.name" placeholder="">
          </Input>
        </FormItem>
        <FormItem prop="phone" label=":">
          <Input type="text" v-model="updateReceiptInfo.phone" placeholder="">
          </Input>
        </FormItem>
        <FormItem prop="telephoneArea" label=":">
          <Input type="text" v-model="updateReceiptInfo.telephoneArea" placeholder="">
          </Input>
        </FormItem>
        <FormItem prop="telephone" label=":">
          <Input type="text" v-model="updateReceiptInfo.telephone" placeholder="">
          </Input>
        </FormItem>
        <FormItem prop="telephoneExtension" label=":">
          <Input type="text" v-model="updateReceiptInfo.telephoneExtension" placeholder="">
          </Input>
        </FormItem>
        <FormItem prop="postcode" label=":">
          <Input type="text" v-model="updateReceiptInfo.postcode" placeholder="">
          </Input>
        </FormItem>
        <FormItem prop="country" label=":">
          <Input type="text" v-model="updateReceiptInfo.country" placeholder="">
          </Input>
        </FormItem>
        <FormItem prop="updateProvinceCityArea" label="//:">
	          <Cascader :data="threeCityData"  @on-change="updateProvinceAndCityAndArea" v-model="updateProvinceCityArea" trigger="hover"></Cascader>
	    </FormItem>
        <FormItem prop="address" class="form-item"  label=":">
	          <Input type="text" v-model="updateReceiptInfo.address" placeholder="">
	          </Input>
	    </FormItem>
        <FormItem prop="isDefault" label=":">
          <Select v-model="updateReceiptInfo.isDefault" transfer size="large" style="width:100px">
              <Option v-for="item in isDefaultList" :value="item.id" :key="item.id">{{ item.value }}</Option>
          </Select>
        </FormItem>
      </Form>
      <div slot='footer'>
        <Button  @click='updateCancel'></Button>
        <Button type='primary' :loading='updateLoading'>
          <span v-if="!updateLoading" @click='updateSure'></span>
          <span v-else>Loading...</span>
        </Button>
      </div>
    </Modal>
    <!--end -->
      <Table border :columns='receiptInfoColumns' :data='receiptInfoList' ref='table' size="small"></Table>
      <!-- <br/>
      <Button type="primary" size="large" @click="exportData"><Icon type="ios-download-outline"></Icon></Button> -->
        <div style='display: inline-block;float: right; margin-top:10px;'>
        <Page style='margin-right:10px;' @on-page-size-change="onPageSizeChange" show-sizer :page-size-opts='params.pageSizeOpts' :current="params.currentPage"  :total='params.total' :pageSize='params.pageSize' ref='page' :show-total='true'   @on-change='selectPage' show-elevator ></Page>
      </div>
    </div>
</template>
<script>
import threeCity from "@/components/service/threecitylist"
export default {
  name: 'ReceiptInfo',
  data () {
    return {
    //
      threeCityData:threeCity.getThreeCity(),
      //
      addProvinceCityArea:[],
      //
      updateProvinceCityArea:[],
        params:{
            pageSizeOpts:[10,20,50,100,500,1000],//
            startNum:1,//
            currentPage:1,//
            pageNum:1,//
            pageSize:10,//
            total:0//
        },
        //1,2
      isDefaultList:[
            {id:1,value:''},
          {id:2,value:''},
        ],
        //
        addReceiptInfoModel:false,
        addLoading:false,
        addReceiptInfoRules: {
            name: [
                {required: true, message: '', trigger: 'blur'}
                ],
            },
        addReceiptInfo:{
        },
    //
        updateReceiptInfoModel:false,
        updateLoading:false,
        updateReceiptInfoRules: {
            name: [
                {required: true, message: '', trigger: 'blur'}
                ],
            },
        updateReceiptInfo:{
    },
        //
        deleteReceiptInfo:{},
	    receiptInfoList: [],
	    receiptInfoColumns: [
        {
          title: '',
          align:'center',
          render: (h, params) => {
            return h('span', params.index
            +(this.params.currentPage-1)*this.params.pageSize+this.params.startNum);
          }
        },
        {
          title: 'id',
          key: 'receiptInfoId',
          align:'center'
        },
        {
            title:'',
            key:'name',
            align:'center'
        },
        {
            title:'',
            key:'phone',
            align:'center'
        },
        {
            title:'',
            key:'telephoneArea',
            align:'center'
        },
        {
            title:'',
            key:'telephone',
            align:'center'
        },
        {
            title:'',
            key:'telephoneExtension',
            align:'center'
        },
        {
            title:'',
            key:'postcode',
            align:'center'
        },
        {
            title:'',
            key:'country',
            align:'center'
        },
        {
            title:'',
            align:'center',
            render:(h,params)=>{
                
                var r=params.row.province+params.row.city+params.row.area
                return h("div",r)
            }
        },
        {
        	title:'',
        	key:'address',
          align:'center'
        },
        {
        	title:'',
            key:'isDefault',
             minWidth:100,
          align:'center',
          render: (h, params) => {
            let isDefaultvalue="";
            this.isDefaultList.forEach(element => {
              if(element.id==params.row.isDefault){
                isDefaultvalue=element.value;
              }
            });
             return  h('span',isDefaultvalue);
          }
        },
		{
          title: '',
          key: 'action',
          align:'center',
          render: (h, params) => {
            var varhh1=  h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  margin: '5px'
                },
                on: {
                  click: () => {
                    this.update(params.row)
                  }
                }
              }, '');
            var varhh2=  h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                style: {
                  margin: '5px'
                },
                on: {
                  click: () => {
                    this.delete(params.row)
                  }
                }
              }, '');
              let hh12=h("div","");
              if(this.isSuperAdmin){

                  hh12=h("div",[
                      varhh1,
                      varhh2
                  ]);
              }
            return hh12;
        }
        }
      ],
    }
  },
  methods: {
       //
	addProvinceAndCityAndArea(value,label){
        this.addReceiptInfo.province=label[0].label
        this.addReceiptInfo.city=label[1].label
        this.addReceiptInfo.area=label[2].label
    },
       //
	updateProvinceAndCityAndArea(value,label){
        this.updateReceiptInfo.province=label[0].label
        this.updateReceiptInfo.city=label[1].label
        this.updateReceiptInfo.area=label[2].label
    },
    //
    selectPage (currentPage) {
      this.params.currentPage=currentPage;
      this.params.pageNum = (this.params.currentPage-1)*this.params.pageSize+this.params.startNum;
      //path
     let pp=JSON.stringify({
       currentPage:currentPage,
        accountId:JSON.parse(this.$route.params.pathParams).accountId
     })
     //console.log(this.$route.path.substr(0,this.$route.path.indexOf(this.$route.params.pathParams)))
      this.$router.push(this.$route.path.substr(0,this.$route.path.indexOf(this.$route.params.pathParams))+pp);
      this.getList()
    },
    //
    onPageSizeChange(pageSize){
      this.getList(pageSize)
    },
  //
   getList (pageSize) {
     /**
     * 
     * $this  vue
     * p.countUrl url
     * p.listUrl url
     * p.data 
     */
     this.params.pageSize=pageSize||this.params.pageSize
     this.params.accountId=JSON.parse(this.$route.params.pathParams).accountId
     this.axiosbusiness.getList(this,{
       countUrl:'/receiptInfo/count',
       listUrl:'/receiptInfo/list',
       data:'receiptInfoList'
     },this.params)
    },
  //
	 add (params) {
      this.addReceiptInfoModel = true
      this.addReceiptInfo.isDefault=2
    },
		//
		 addCancel () {
      if (!this.addLoading) {
        this.addReceiptInfoModel = false
        this.$refs.addReceiptInfo.resetFields()
      }
    },
		//
    addSure () {
       /**
     * 
     * $this  vue
     * p.ref 
     * p.url url
     * p.requestObject 
     * p.loading loading
     * p.showModel 
     */
    this.addReceiptInfo.accountId=JSON.parse(this.$route.params.pathParams).accountId
    this.axiosbusiness.add(this,{
      ref:'addReceiptInfo',
      url:'/receiptInfo/add',
      requestObject:'addReceiptInfo',
      loading:'addLoading',
      showModel:'addReceiptInfoModel'
    })
    },
	 update (params) {
      this.updateReceiptInfoModel = true
      //
      this.axiosbusiness.get(this,{
         url:'/receiptInfo/load?receiptInfoId='+params.receiptInfoId,
         data:'updateReceiptInfo',
         success:()=>{
             let province=this.updateReceiptInfo.province
             let city=this.updateReceiptInfo.city
             let area=this.updateReceiptInfo.area
            this.updateProvinceCityArea=threeCity.getValueByLabel(province,city,area)

         }
       })
    },
		//
		 updateCancel () {
      if (!this.updateLoading) {
        this.updateReceiptInfoModel = false
        this.$refs.updateReceiptInfo.resetFields()
      }
    },
		//
    updateSure () {
      /**
     * 
     * $this  vue
     * p.ref 
     * p.url url
     * p.requestObject 
     * p.loading loading
     * p.showModel 
     */
    this.axiosbusiness.update(this,{
      ref:'updateReceiptInfo',
      url:'/receiptInfo/update',
      requestObject:'updateReceiptInfo',
      loading:'updateLoading',
      showModel:'updateReceiptInfoModel'
    })
 
    },
    //
    delete(params){
    /**
     * 
     * $this  vue
     * p.url url
     * p.requestObject 
     */
    this.deleteReceiptInfo={
      "receiptInfoId":params.receiptInfoId
    };
    this.axiosbusiness.delete(this,{
      url:'/receiptInfo/delete',
      requestObject:'deleteReceiptInfo'
    })
    },
    //
   /*  exportData (type) {
      this.$refs.table.exportCsv({
          filename: '',
          columns: this.ReceiptInfoCoumns,
          data: this.ReceiptInfoList
      });
    }       */
  },
   watch: {
    //
      $route (to,from){
        this.selectPage(JSON.parse(this.$route.params.pathParams).currentPage)
      }
    }, 
  created () {
       //
    this.isSuperAdmin=this.business.getIsSuperAdmin()
    this.selectPage(JSON.parse(this.$route.params.pathParams).currentPage)
  },
  mounted () {

  }
}
</script>


